#MainHeader {
    background-color: white;
    height: 75px;
    width: 100%;
    position: fixed;
    top: 0;
    display: flex;
    align-items: center;
    z-index: 12;
}

#MainHeader > p {
    margin: 0 auto;
}

#TitleContainer {
    font-size: 2em;
    text-align: left;
    width: 70%;
    margin: 0 auto;
}

#TitleContainer > p {
    margin: 0;
}
