#MenuToggle {
    cursor: pointer;
    width: 100%;
    height: 30px;
    background-color: white;
    border-radius: 0 0 15px 15px;
    top: 75px;
    position: fixed;
    z-index: 10;
    box-shadow: 0px -4px 6px 4px #00000080;
    transition: transform 0.5s ease-in;
}

#MenuToggle:hover {
    background-color: #f3f3f3;
}

#MenuToggle.expanded {
    transform: translateY(300px);
}

#MenuToggle.collapsed {
    transform: translateY(0);
}

#MenuContainer {
    display: grid;
    grid-template-columns: 20% 1fr;
    width: 100%;
    position: fixed;
    z-index: 11;
    height: 300px;
    background-color: #d0d0d0;
    top: -225px;
    transition: transform 0.5s ease-in;
    box-shadow: inset 0px 2px 4px #00000040;
}

#MenuButtonContainer {
    display: flex;
    flex-direction: column;
}

#MenuButtonContainer > button {
    height: 50%;
    cursor: pointer;
    background-color: #3d3d3d;
    color: white;
    border: none;
}

#MenuButtonContainer > button.active {
    background-color: #242424;
    text-shadow: white 0 0 1px;
}
